<template>
    <div class="wrapper">
        <div class="section">
            <div class="top_navbar">
                <div class="hamburger" v-if="hamburger == true">
                    <a>
                        <i class="fas fa-bars" v-on:click="menu_var_f"></i>
                    </a>
                </div>

            </div>

        </div>
        <div class="sidebar">
            <div class="profile">
                <img src="../../public/alh.png" alt="profile_picture">
                <h3>알로항</h3>
                <p>디스코드를 더 편하게.</p>
            </div>
            <ul>
                <li v-for="(a, i) in name_list" :key="i">
                    <router-link v-bind:class="name_list[i].split('|')[1] !== undefined ? 'activen' : 'active'"
                        :to="link_list[i]">
                        <span class="icon"><i v-bind:class=font_list[i]></i></span>
                        <span class="now_site">{{ name_list[i].replace("|", "") }}</span>
                    </router-link>
                </li>
            </ul>

        </div>
    </div>
    <!-- ///////////////////////////////////////////////////// -->
    <div class="commandsd" @click="menu_var_ff">
        <img src="../../public/alh.png" alt="알로항">
        <div class="main_ds">
            <div class="title">알로항 (알로항#4559)</div>
            <div class="ds"><br>"디스코드를 더 편하게."<br><br>필요한 기능이지만 디스코드엔 없는 기능이라면 알로항에서 찾아보세요</div>
            <br>
            <button class="main_bt" v-for="(a, i) in button_name_list" :key="i"
                @click.ctrl="linkto(1, button_to_list[i])" @click.exact="linkto(2, button_to_list[i])">{{
                        button_name_list[i]
                }}</button>
        </div>
        <!--  -->
    </div>
</template>

<script>

export default {
    name: "MyAllohang",
    data() {
        return {
            hamburger: false,
            font_list: ["fas fa-home", "fas fa-pills", "fas fa-cog fa-spin", "fas fa-crosshairs", "fas fa-money-bill", "fas fa-grin-beam", "fas fa-chart-bar", "fas fa-images", "fas fa-list", "fas fa-database"],
            name_list: ["|메인", "일반 명령어", "서버 세팅 명령어", "서버 관리 명령어", "도박 명령어", "재미 명령어", "통계 명령어", "짤 명령어", "기타 명령어", "데이터 삭제"],
            link_list: ["/", "/gernal", "/setting", "/manage", "/gambling", "/fun", "/stat", "/meme", "/app", "/data"],
            button_name_list: ["봇 초대하기", "서포트 서버"],
            button_to_list: ["https://discord.com/api/oauth2/authorize?client_id=826698986970677278&permissions=8&scope=bot%20applications.commands", "https://discord.gg/W9qea6yEXT"]
        };
    },
    methods: {
        linkto(type, link) { //1: 새로 열기 2: 이동하기
            if (type == 1) { //
                window.open(link)
                return
            }
            if (type == 2) {
                window.location.href = link
            }
        },
        // test() {
        //     console.log('../../img/example/' + this.command_name[0].replace(' ', '') + '.gif');
        // },
        menu_var_f() {
            if (document.querySelector("body").classList.toggle("active") == true) {
                // this.title = true
                // 넣음
                this.hamburger = true
            } else {
                this.hamburger = false
                // this.title = false
                //뺌
            }
            // console.log(document.querySelector("body").classList.toggle("active"));//.classList.toggle("active");
            // document.querySelector("body").classList.toggle("active")
        },
        menu_var_ff() {
            if (document.querySelector("body").classList.value == "") {
                document.querySelector("body").classList.toggle("active")
                this.hamburger = true
            }
        },
    },
    components: {},
};
</script>

<style>
