}

@import url("https://fonts.googleapis.com/css2?family=Open+Sans&display=swap");

* {
    list-style: none;
    text-decoration: none;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Open Sans", sans-serif;
}

body {
    margin: 0;
    padding: 0;
    background: #f5f6fa;
}

.activen {
    border-right: 2mm solid blue;
}

.fa-bars {
    cursor: Pointer
}

.wrapper .sidebar ul li a span:hover {
    color: #3498DB;

}

.wrapper .sidebar {
    z-index: 1;
    overflow-y: scroll;
    background: rgb(5, 68, 104);
    position: fixed;
    top: 0;
    left: 0;
    width: 225px;
    height: 100%;
