}

.wrapper .sidebar {
    -ms-overflow-style: none;
    scrollbar-width: none;
    overflow-y: scroll;
}

body {
    -ms-overflow-style: none;
}

::-webkit-scrollbar {
    display: none;
}



.wrapper .sidebar::-webkit-scrollbar {
    display: none;
}

.wrapper .sidebar .profile img {
    display: block;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin: 0 auto;
}

.wrapper .sidebar .profile h3 {
    color: #ffffff;
    margin: 10px 0 5px;
}

.wrapper .sidebar .profile p {
    color: rgb(206, 240, 253);
    font-size: 14px;
}

.wrapper .sidebar ul li a {
    text-align: left;
    word-break: keep-all;
    display: block;
    padding: 13px 30px;
    border-bottom: 1px solid #10558d;
    color: rgb(241, 237, 237);
    font-size: 16px;
    position: relative;
}

.wrapper .sidebar ul li a .icon {
    color: #dee4ec;
    width: 30px;
    display: inline-block;
}

.wrapper .section {
    width: calc(100% - 225px);
