}

.wrapper .section .hamburger a {
    font-size: 28px;
    color: #f4fbff;
}

.wrapper .section .hamburger a:hover {
    color: #a2ecff;
}

body.active .wrapper .sidebar {
    overflow-y: scroll;
    left: -225px;
}

body.active .wrapper .section {
    margin-left: 0;
    width: 100%;
}


.nav {
    background: darkslateblue;
    padding: 15px;
    border-radius: 5px;
}

