    margin: 5%
        /* top: 10% */
}

.main_ds .title {
    font-family: "Arial Black", sans-serif;
    word-break: keep-all;
    font-weight: bold;
    font-size: 2.8rem !important;
    text-shadow: 0.5px 0.5px 1px skyblue;
}

.main_ds .ds {
    font-family: "Arial Black", sans-serif;
    word-break: keep-all;
    font-weight: bold;
    font-size: 1.4rem;
    text-shadow: 0.5px 0.5px 1px skyblue;

}

/*  */
.main_ds .main_bt {
    margin: 1.5rem;
    background: #1aab8a;
    color: #fff;
    border: none;
    position: relative;
    height: 60px;
    font-size: 1.6em;
    padding: 0 2em;
    cursor: pointer;
    transition: 800ms ease all;
    outline: none;
}

.main_ds .main_bt:hover {
    background: #fff;
    color: #1aab8a;
}

.main_ds .main_bt:before,
.main_ds .main_bt:after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    height: 2px;
    width: 0;
    background: #1aab8a;
    transition: 400ms ease all;
}

.main_ds .main_bt:after {
    right: inherit;
    top: inherit;
    left: 0;
    bottom: 0;
}

.main_ds .main_bt:hover:before,
.main_ds .main_bt:hover:after {
    width: 100%;
    transition: 800ms ease all;
}


